import React from 'react';
import { Form } from 'react-bootstrap';
import Col from 'react-bootstrap/Col';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';
import TagSelect from "../TagSelect";
import Select from 'react-select';

const NAME_MAX_LENGTH = 80;

const STATUS_OPTIONS =
  ['Planning Phase', 'Design Phase', 'Development Phase', 'Testing Phase', 'Deployment Phase', 'Looking for Open Source Contributions']
    .map(status => ({ value: status, label: status }));

const AUTO_CHAT_INDICATOR = '_';
const ChatType = {
  CUSTOM: 'custom',
  AUTO: 'auto'
};

export default class EditableProject extends React.Component {
  constructor(props) {
    super(props);

    this.onChange = this.onChange.bind(this);
    this.convertToOption = this.convertToOption.bind(this);
    this.getChatType = this.getChatType.bind(this);
    this.changeChatType = this.changeChatType.bind(this);
    this.renderChatChannelInput = this.renderChatChannelInput.bind(this);
  }

  onChange(field, value) {
    this.props.onProjectFieldEdit(field, value);
  }

  convertToOption(item) {
    return {
      label: item,
      value: item
    };
  }

  getChatType() {
    const { discordUrl } = this.props.project;
    if (discordUrl === AUTO_CHAT_INDICATOR) {
      return ChatType.AUTO;
    }
    return ChatType.CUSTOM;
  }

  changeChatType(type) {
    this.props.onProjectFieldEdit(
      'discordUrl',
      type === ChatType.AUTO ? AUTO_CHAT_INDICATOR : ''
    );
  }

  renderChatChannelInput() {
    const { project, isCreateView } = this.props;

    if (!isCreateView) {
      return (
        <Form.Control
          placeholder="Link to a chat channel like Discord (will only be shown to project members)"
          value={project.discordUrl}
          onChange={event => this.onChange('discordUrl', event.target.value)}
        />
      );
    }

    return (
      <>
        <Form.Row>
          <ToggleButtonGroup
            type="radio"
            name="chatTypeToggle"
            value={this.getChatType()}
            className="chat-type-toggle"
            onChange={this.changeChatType}
          >
            <ToggleButton
              value={ChatType.AUTO}
              size="sm"
              variant="light"
            >
              Create a public Discord for me
          </ToggleButton>
            <ToggleButton
              value={ChatType.CUSTOM}
              size="sm"
              variant="light"
            >
              I'll provide my own URL
          </ToggleButton>
          </ToggleButtonGroup>
        </Form.Row>
        <Form.Control
          placeholder="Link to a chat channel like Discord (will only be shown to project members)"
          value={
            project.discordUrl === AUTO_CHAT_INDICATOR
              ? 'Will be created automatically'
              : project.discordUrl
          }
          onChange={event => this.onChange('discordUrl', event.target.value)}
          disabled={this.getChatType() === ChatType.AUTO}
        />
      </>
    );
  }

  render() {
    const { project } = this.props;

    return (
      <Form>
        <h4><span></span> Name <small>(required)</small></h4>
        <Form.Control
          value={project.name}
          placeholder="A short title"
          onChange={event => this.onChange('name', event.target.value)}
          maxLength={NAME_MAX_LENGTH}
        />
        <hr />
        <h4><span></span> Description</h4>
        <Form.Control
          as="textarea"
          rows="5"
          value={project.description || ''}
          placeholder={'The project description is shown publicly to'
            + ' users who are interested in your project'
            + ' (Markdown is supported).'}
          onChange={event => this.onChange('description', event.target.value)}
        />
        <hr />
        <h4><span></span> Directions</h4>
        <Form.Control
          as="textarea"
          rows="5"
          value={project.directions || ''}
          placeholder={'Directions are shown only to project members,'
            + ' and should include information like onboarding directions,'
            + ' GitHub repositories, etc (Markdown is supported).'}
          onChange={event => this.onChange('directions', event.target.value)}
        />
        <hr />

        <h4><span></span> Chat Channel</h4>
        {this.renderChatChannelInput()}
        <hr />

        <h4><span></span> Skills</h4>
        <TagSelect
          type="skills"
          value={project.languages}
          onChange={languages => this.onChange('languages', languages)}
        />
        <hr />
        <h4><span></span> Interest Areas</h4>
        <TagSelect
          type="interests"
          value={project.interests}
          onChange={interests => this.onChange('interests', interests)}
        />
        <hr />
        <h4><span></span> Joinable By Others?</h4>
        <Form.Control
          as="select"
          onChange={event => this.onChange('selfJoinable', event.target.value === 'true')}
          value={project.selfJoinable}
        >
          <option value={true}>Yes! Anyone can join on their own.</option>
          <option value={false}>No, people need to ask before joining.</option>
        </Form.Control>
        <hr />
        <h4><span></span> Project Status</h4>
        <Select
          type="status"
          options={STATUS_OPTIONS}
          value={this.convertToOption(project.status)}
          onChange={status => this.onChange('status', status.value)}
        />
        <hr />
        <h4><span></span> Hidden from others?</h4>
        <Form.Control
          as="select"
          onChange={event => this.onChange('type', event.target.value)}
          value={project.type}
        >
          <option value="public">No! The project will be visible to all!</option>
          <option value="private">Yes, only team members can see the project.</option>
        </Form.Control>
      </Form>
    );
  }
}
